/* App.css */

.calculator {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #2E236C; /* Background color */
  color: #ffffff;
  font-family: Arial, sans-serif;
}

.calculator input[type="text"] {
  width: 320px;
  height: 60px;
  font-size: 2rem;
  text-align: right;
  margin-bottom: 10px;
  padding: 10px;
  border: none;
  border-radius: 8px;
  background-color: #17153B;
  color: #C8ACD6; /* Text color */
  outline: none;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}

button {
  width: 70px;
  height: 70px;
  font-size: 1.5rem;
  border: none;
  border-radius: 8px;
  color: #ffffff;
  cursor: pointer;
  background-color: #433D8B; /* Button color */
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #5a4dbf; /* Hover color */
}

button:active {
  background-color: #3d318b; /* Active color */
}

button:nth-child(4n) {
  background-color: #C8ACD6; /* Operator buttons color */
  color: #2E236C;
}

button:nth-child(4n):hover {
  background-color: #b09bb7;
}
